import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import {  NewtisuserComponent } from './managetisuser/newtisuser/newtisuser.component';

const routes: Routes = [
  {
    path : 'newtisuser',
    loadChildren: () => import('./managetisuser/newtisuser/newtisuser.module').then(m => m.NewtisuserModule)
  },
  {
    path : 'newtisuser/edit/:toc/:id',
    loadChildren: () => import('./managetisuser/newtisuser/newtisuser.module').then(m => m.NewtisuserModule)
  },
  {
    path : 'userlist',
    loadChildren: () => import('./managetisuser/userlist/userlist.module').then(m => m.userListModule)
  },
  {
    path : 'userhelp',
    loadChildren: () => import('./shared/userhelp/userhelp.module').then(m => m.UserhelpModule)
  },
  {
    path: '',
    redirectTo: 'userlist',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes), HttpClientModule],
  exports: [RouterModule]
})
export class AppRoutingModule { }
