import { Injectable, Injector, InjectionToken, Inject } from '@angular/core';
import {
  HttpInterceptor,
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry, timeout } from 'rxjs/operators';
import { Constants } from '../utils/constants';
import { ErrorConstants } from '../utils/errorconstants';
import { v4 as uuid } from 'uuid';
import { ApiValidationError } from '../model/ApiValidationError';
import { ApiError } from '../model/ApiError';
import { SystemError } from '../model/SystemError';
import { NGXLogger } from 'ngx-logger';
import { EEXIST } from 'constants';
import { ErrorLogging } from '../model/ErrorLogging';

export const DEFAULT_TIMEOUT = new InjectionToken<number>('defaultTimeout');

/**
 * Implements HTTP_INTERCEPTOR in order to handle error
 * while making any http request to any REST API service.
 */
@Injectable({
  providedIn: 'root'
})
export class APIInterceptorService implements HttpInterceptor {
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const timeoutValue = Number(request.headers.get('timeout')) || this.defaultTimeout;

    /** Generate UID to trace a single request thread. */
    const UID = uuid();

    /** Adding generated UID to request header to trace sigle request thread. */
    const requestWithHeader = request.clone({
      setHeaders: { CID: UID }
    });

    return next.handle(requestWithHeader).pipe(
      timeout(timeoutValue),
      catchError(this.handleError)
    );
  }

  /**
   *
   * @param defaultTimeout Provide default timeout duration.
   */
  constructor(@Inject(DEFAULT_TIMEOUT) protected defaultTimeout: number) { }

  /**
   * Method to Handle error while making get request to Rest API.
   * @param err Variable to hold httpErrorResponse.
   */
  private handleError(err: HttpErrorResponse) {
    /** Send error captured to APM server using apmAgent */
    let erroMessageLogging = new ErrorLogging();
    let apiErrors: ApiError;
    let sysErrors: SystemError;
    if (!navigator.onLine) {
      // Handle offline error
      erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_NO_INTERNET_PRIMARY + '$' + ErrorConstants.MESSAGE_NO_INTERNET_SECONDARY;
    }
   else if (err.error instanceof ErrorEvent) {
      // Client side or netwrok error occured.
      erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_CLIENTSIDE_ERROR + err.error.message;
    }
    else {
      if (err.status == Constants.HTTP_STATUS_BADREQUEST)
      {
        apiErrors = <ApiError> err.error;
        erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_INVALID_VALUE_SUPPLIED_PRIMARY + '$' +
        ErrorConstants.MESSAGE_INVALID_VALUE_SUPPLIED_SECONDARY;
        erroMessageLogging.errorObject = apiErrors.debugMessage;
      }
      else if (err.status == Constants.HTTP_STATUS_CONFLICT) {
        sysErrors = <SystemError> err.error;
        erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_USER_ALREADY_EXISTS_PRIMARY + '$' +
        ErrorConstants.MESSAGE_USER_ALREADY_EXISTS_SECONDARY;
        erroMessageLogging.errorObject = sysErrors.message;
      }
      else
       {
        erroMessageLogging.errorObject = err.message;
        switch (err.status) {
          // Server side error based on status returned from API
          case Constants.API_POST_ERROR_0: {
            erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_SERVER_UNREACHABLE_PRIMARY + '$' +
            ErrorConstants.MESSAGE_SERVER_UNREACHABLE_SECONDARY;
            break;
          }
          case Constants.HTTP_STATUS_UNAUTHORIZED_ACCESS: {
            erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_UNAUTHORIZED_ACCESS_PRIMARY + '$' +
            ErrorConstants.MESSAGE_UNAUTHORIZED_ACCESS_SECONDARY;
            break;
          }
          case Constants.HTTP_STATUS_FORBIDDEN: {
            erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_FORBIDDEN_PRIMARY + '$' +
            ErrorConstants.MESSAGE_FORBIDDEN_SECONDARY;
            break;
          }
          case Constants.HTTP_STATUS_NOT_ALLOWED: {
            erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_HTTPMETHOD_NOT_ALLOWED_PRIMARY + '$' +
            ErrorConstants.MESSAGE_HTTPMETHOD_NOT_ALLOWED_SECONDARY;
            break;
          }
          case Constants.HTTP_STATUS_PAGE_NOT_FOUND: {
            erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_PAGE_NOT_FOUND_PRIMARY + '$' +
             ErrorConstants.MESSAGE_PAGE_NOT_FOUND_SECONDARY;
            break;
          }
          default: {
            erroMessageLogging.errorMessage = ErrorConstants.MESSAGE_SERVER_ERROR + ':' + err.message;
            break;
          }
        }
      }
    }
    return throwError(erroMessageLogging);
  }
}
