import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { of, Observable } from 'rxjs';
import { ErrorConstants } from '../utils/errorconstants';
import { NewUser } from '../model/tisuser';
import { Toc } from '../model/Toc';
import { catchError } from 'rxjs/operators';
import { timeout } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { constants } from 'os';
import { Constants } from '../utils/constants';
import { Role } from '../model/Role';
import { UserDetails } from '../model/UserDetails';
import { PasswordChange } from '../model/PasswordChange';
import { strictEqual } from 'assert';
import { UserStatus } from '../model/UserStatus';


/**
 * Provides service for accessing REST API service for getting arrivals details.
 */
@Injectable({
  providedIn: 'root'
})

export class UserManagementService {
  /** URL for new user post call. */
  private saveUserURL = environment.save_user_api_url;
  /** URL for saving force password change value post call. */
  private saveForcePasswordChangeURL = environment.save_force_password_change_api_url;
  /** URL for saving changed status of user post call. */
  private saveUserStatusURL = environment.save_user_status_api_url;
  /** URL for update user post call. */
  private updateUserURL = environment.update_user_api_url;
  /** URL for get Toc list call. */
  private getTocsListURL = environment.get_tocs_api_url;
  /** URL for get Roles call. */
  private getRolesURL = environment.get_roles_api_url;
  /** URL for getting all the users for a said TOC. */
  private getUsersURL = environment.get_users_api_url;
  /** URL for geting a particular user details for edit. */
  private getUserURL = environment.get_user_api_url;

  /**
   * Constructor to allow the injection of required services.
   * @param http HttpClient instance for accessing the Rest API.
   */
  constructor(private http: HttpClient) { }
  // Variable to return response for defined test criterias
  respseReturn: string;
  // Variable to hold test condition based on first name and last name
  testConditions: string = '';

  /**
   * Method to post new user details to server using post Api.
   * @param opost - NewUser object
   */
  postUserDetails(opost: NewUser): Observable<any> {
    // Create test conditions if actual API is not in place
    this.testConditions = opost.firstName + opost.lastName;
    switch (this.testConditions) {
      case 'FujitsuXXTestTestXXFujitsu201': {
        this.respseReturn = Constants.USER_SAVE_SUCCESS_PRIMARY + '$' +
          Constants.USER_SAVE_SUCCESS_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu400': {
        this.respseReturn = ErrorConstants.MESSAGE_INVALID_VALUE_SUPPLIED_PRIMARY + '$' +
          ErrorConstants.MESSAGE_INVALID_VALUE_SUPPLIED_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu401': {
        this.respseReturn = ErrorConstants.MESSAGE_UNAUTHORIZED_ACCESS_PRIMARY + '$' +
          ErrorConstants.MESSAGE_UNAUTHORIZED_ACCESS_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu403': {
        this.respseReturn = ErrorConstants.MESSAGE_FORBIDDEN_PRIMARY + '$' +
          ErrorConstants.MESSAGE_FORBIDDEN_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu405': {
        this.respseReturn = ErrorConstants.MESSAGE_HTTPMETHOD_NOT_ALLOWED_PRIMARY + '$' +
          ErrorConstants.MESSAGE_HTTPMETHOD_NOT_ALLOWED_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu409': {
        this.respseReturn = ErrorConstants.MESSAGE_USER_ALREADY_EXISTS_PRIMARY + '$' +
          ErrorConstants.MESSAGE_USER_ALREADY_EXISTS_SECONDARY;
        break;
      }
      default: {
        // Actual API call to add new user
        return this.http.post(this.saveUserURL, opost);
        break;
      }
    }
    return of(this.respseReturn);
  }

  /**
   * Method to post force password change value for user to server using post Api.
   * @param toc - TOC code
   * @param userId - Employee ID
   * @param opost - PasswordChange object
   */
  postForcePasswordChange(toc: string, userId: string, opost: PasswordChange): Observable<any> {
    return this.http.post(this.saveForcePasswordChangeURL + Constants.TOC + toc.toUpperCase() + Constants.USERS +
      userId + Constants.FORCE_PWD_CHANGE, opost);
  }

  /**
   * Method to post user status value for user to server using post Api.
   * @param toc - TOC code
   * @param userId - Employee Id
   * @param opost - UserStatus object
   */
  postChangeUserStatus(toc: string, userId: string, opost: UserStatus): Observable<any> {
    return this.http.post(this.saveUserStatusURL + Constants.TOC + toc.toUpperCase() + Constants.USERS +
      userId + Constants.ACTIVE_STATUS, opost);
  }

  /**
   * Method to post update user details to server using post Api.
   * @param opost - UserDetails object
   */
  postUpdateUserDetails(opost: UserDetails): Observable<any> {
    // Create test conditions if actual API is not in place
    this.testConditions = opost.firstName + opost.lastName;
    switch (this.testConditions) {
      case 'FujitsuXXTestTestXXFujitsu201': {
        this.respseReturn = Constants.USER_UPDATED_SUCCESS_PRIMARY + '$' +
          Constants.USER_UPDATED_SUCCESS_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu400': {
        this.respseReturn = ErrorConstants.MESSAGE_INVALID_VALUE_SUPPLIED_PRIMARY + '$' +
          ErrorConstants.MESSAGE_INVALID_VALUE_SUPPLIED_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu401': {
        this.respseReturn = ErrorConstants.MESSAGE_UNAUTHORIZED_ACCESS_PRIMARY + '$' +
          ErrorConstants.MESSAGE_UNAUTHORIZED_ACCESS_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu403': {
        this.respseReturn = ErrorConstants.MESSAGE_FORBIDDEN_PRIMARY + '$' +
          ErrorConstants.MESSAGE_FORBIDDEN_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu405': {
        this.respseReturn = ErrorConstants.MESSAGE_HTTPMETHOD_NOT_ALLOWED_PRIMARY + '$' +
          ErrorConstants.MESSAGE_HTTPMETHOD_NOT_ALLOWED_SECONDARY;
        break;
      }
      case 'FujitsuXXTestTestXXFujitsu404': {
        this.respseReturn =
          ErrorConstants.MESSAGE_PAGE_NOT_FOUND_PRIMARY + '$' +
          ErrorConstants.MESSAGE_PAGE_NOT_FOUND_SECONDARY;
        break;
      }
      default: {
        // Actual API call to update user
        return this.http.put<any>(this.updateUserURL, opost, {
          params: new HttpParams().set('toc', opost.toc.toUpperCase()).set('userId', opost.employeeId)
        });
        break;
      }
    }
    return of(this.respseReturn);
  }

  /**
   * Method to get list of Roles based on TOC code using get Api for roles.
   * @param selectedToc - TOC code
   * @param selectedAppId - Application Id
   */
  getUserRoles(selectedToc: string, selectedAppId: number): Observable<Role[]> {
    if (selectedAppId != null) {
      return this.http.get<Role[]>(this.getRolesURL, {
        params: new HttpParams().set('toc', selectedToc.toUpperCase())
          .set('appId', selectedAppId.toString())
      });
    }
    else {
      return this.http.get<Role[]>(this.getRolesURL, {
        params: new HttpParams().set('toc', selectedToc.toUpperCase())
      });
    }
  }

  /**
   * Method to get list of all TOCs using get Api for TOCs.
   */
  getTocs(): Observable<Toc[]> {
    return this.http.get<Toc[]>(this.getTocsListURL);
  }

 /**
  * Method to get list of all Users with their details using get Api for Users.
  * @param getUsersToc - TOC Code
  */
  getUsersDetails(getUsersToc: string): Observable<UserDetails[]> {
    return this.http.get<UserDetails[]>(this.getUsersURL, {
      params: new HttpParams().set('toc', getUsersToc.toUpperCase())
    });
  }

  /**
   * Method to get user detail based on Employee ID and TOC using get Api for User.
   * @param getUsersToc - TOC Code
   * @param getEmployeeID - Employee ID
   */
  getUserDetails(getUsersToc: string, getEmployeeID: string): Observable<UserDetails[]> {
    return this.http.get<UserDetails[]>(this.getUserURL, {
      params: new HttpParams().set('toc', getUsersToc.toUpperCase())
        .set('userId', getEmployeeID.toString())
    });
  }
}
