import { BrowserModule } from '@angular/platform-browser';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { HttpClientModule, HTTP_INTERCEPTORS, HttpClient } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from 'src/app/shared/material.module';
import { LoggerModule } from 'ngx-logger';
import { environment } from 'src/environments/environment';
import { NewtisuserComponent } from './managetisuser/newtisuser/newtisuser.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ToastrModule } from 'ng6-toastr-notifications';
import { UserManagementService } from './shared/service/user-management.service';
import { APIInterceptorService, DEFAULT_TIMEOUT } from './shared/service/api-interceptor.service';
import { UserDetailsResolverService } from 'src/app/shared/service/user-details-resolver.service';
import { LandingPageResolverService } from './shared/service/landing-page-resolver.service';
import { ConfirmDialogComponent } from 'src/app/shared/confirm-dialog/confirm-dialog.component';
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

@NgModule({
  declarations: [
    AppComponent,
  ],
  entryComponents: [ConfirmDialogComponent],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    MaterialModule,
    ReactiveFormsModule,
    TranslateModule.forRoot(
      {
        loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
        }
      }),
    AppRoutingModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot(),
    LoggerModule.forRoot(environment.logging),
  ],
  providers: [UserManagementService, UserDetailsResolverService, LandingPageResolverService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: APIInterceptorService,
      multi: true
    },
    {
      provide: DEFAULT_TIMEOUT,
      useValue: 20000
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}